/* 蓝色渐变 */
.blue-linear {
  background: -webkit-linear-gradient(#0171FF, #149BF8);
  background: linear-gradient(#0171FF, #149BF8);
}
/* 绿色渐变 */
.green-linear {
  background: -webkit-linear-gradient(left, #1F9760, #25B772);
  background: linear-gradient(to right, #1F9760, #25B772);
}
/* 超出显示省略号 */
.beyond-scope {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/* 超出显示省略号（默认超出2行） */
.beyond-scope-clamp {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
/* 滚动条样式 */
.scroll-style {
  scrollbar-track-color: transparent;
  scrollbar-arrow-color: rgba(185, 191, 203, 0.3);
  scrollbar-3dlight-color: transparent;
  scrollbar-shadow-color: transparent;
  scrollbar-dark-shadow-color: transparent;
  scrollbar-highlight-color: transparent;
  scrollbar-face-color: rgba(185, 191, 203, 0.3);
}
.scroll-style ::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
  background: none;
}
.scroll-style ::-webkit-scrollbar-track {
  background: none;
}
.scroll-style ::-webkit-scrollbar-thumb {
  border-radius: 0.5rem;
  background-color: rgba(0, 0, 0, 0.3);
}
/* placeholder样式 */
.placeholder-style::-webkit-input-placeholder {
  color: #fff;
}
.placeholder-style:-moz-placeholder {
  color: #fff;
}
.placeholder-style::-moz-placeholder {
  color: #fff;
}
.placeholder-style:-ms-input-placeholder {
  color: #fff;
}
/* 列表样式 */
.cellStyle .outer_title {
  width: 100%;
  padding: 2%;
}
.cellStyle .outer {
  width: 96%;
  display: flex;
  min-height: 5rem;
  font-size: 0.8em;
  border-radius: 5px;
  margin: 0.5rem auto;
}
.cellStyle .outer .el-card__body {
  width: 100%;
  padding: 0;
  cursor: pointer;
}
.cellStyle .outer .inner_t,
.cellStyle .outer .inner_b {
  display: flex;
  align-items: center;
  padding: 0.2rem 1rem;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc(100% - 2rem);
  justify-content: space-between;
}
.cellStyle .outer .inner_b {
  height: 50%;
}
.cellStyle .outer .inner_l,
.cellStyle .outer .inner_r {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex: 0.3 1;
  padding: 0.5rem 1rem;
}
.cellStyle .outer .inner_r {
  flex: 0.7 1;
  padding: 0.5rem 1rem;
}
/* tab选显卡样式 */
.tabStyle .el-tabs .el-tabs__header {
  display: block;
}
.tabStyle .el-tabs .el-tabs__header i {
  color: rgba(255, 255, 255, 0.5);
}
.tabStyle .el-tabs .el-tabs__header i:hover {
  color: #fff;
}
.tabStyle .el-tabs .el-tabs__header .el-tabs__nav-wrap:after {
  background: none;
}
.tabStyle .el-tabs .el-tabs__header .el-tabs__item {
  color: rgba(255, 255, 255, 0.5);
}
.tabStyle .el-tabs .el-tabs__header .el-tabs__item.is-active {
  color: #fff;
}
.tabStyle .el-tabs .el-tabs__content {
  height: calc(100% - 55px);
}
/* 取消、确认按钮样式 */
.sureCancelBtn {
  flex: 0.5 1;
  padding: 0;
  margin: 20px;
  border: none;
  height: 40px;
  line-height: 36px;
  border-radius: 5rem;
  background: -webkit-linear-gradient(left, #03DDF8, #35F7AE);
  background: linear-gradient(to right, #03DDF8, #35F7AE);
}
.sureCancelBtn span {
  margin: auto;
  display: block;
  background: #fff;
  border-radius: 5rem;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  color: #606266;
}
/* 文字提示样式 */
.el-tooltip__popper {
  max-width: 30vw;
}
/* 页面整体 */
.body-auto {
  scrollbar-track-color: transparent;
  scrollbar-arrow-color: rgba(185, 191, 203, 0.3);
  scrollbar-3dlight-color: transparent;
  scrollbar-shadow-color: transparent;
  scrollbar-dark-shadow-color: transparent;
  scrollbar-highlight-color: transparent;
  scrollbar-face-color: rgba(185, 191, 203, 0.3);
  width: 100%;
  height: 100%;
  overflow-y: auto;
  font-size: 14px;
  min-width: 768px;
  min-height: 100vh;
  background: #F2F6FC;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  /* loading样式 */
}
.body-auto ::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
  background: none;
}
.body-auto ::-webkit-scrollbar-track {
  background: none;
}
.body-auto ::-webkit-scrollbar-thumb {
  border-radius: 0.5rem;
  background-color: rgba(0, 0, 0, 0.3);
}
.body-auto .pageLoading .circular .path {
  stroke: #35F7AE;
}
.body-auto .pageLoadingClose {
  opacity: 0;
}
.body-auto .pswp {
  z-index: 2147483647;
}
.body-hidden {
  scrollbar-track-color: transparent;
  scrollbar-arrow-color: rgba(185, 191, 203, 0.3);
  scrollbar-3dlight-color: transparent;
  scrollbar-shadow-color: transparent;
  scrollbar-dark-shadow-color: transparent;
  scrollbar-highlight-color: transparent;
  scrollbar-face-color: rgba(185, 191, 203, 0.3);
  width: 100%;
  height: 100%;
  overflow-y: auto;
  font-size: 14px;
  min-width: 768px;
  min-height: 100vh;
  background: #F2F6FC;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  /* loading样式 */
  overflow-y: hidden;
}
.body-hidden ::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
  background: none;
}
.body-hidden ::-webkit-scrollbar-track {
  background: none;
}
.body-hidden ::-webkit-scrollbar-thumb {
  border-radius: 0.5rem;
  background-color: rgba(0, 0, 0, 0.3);
}
.body-hidden .pageLoading .circular .path {
  stroke: #35F7AE;
}
.body-hidden .pageLoadingClose {
  opacity: 0;
}
.body-hidden .pswp {
  z-index: 2147483647;
}
.layout {
  height: 100%;
}
.layout .logo {
  cursor: pointer;
  font-weight: bold;
  color: #52c41a;
  padding: 10px 0 10px 24px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.layout .logo.showLogo {
  padding: 10px 0;
  text-align: center;
}
.layout .logo.showLogo span {
  display: none;
}
.layout .logo:hover {
  color: #DCDFE6;
}
.layout .logo .fa-leaf {
  font-size: 1.5em;
  margin-right: 0.5rem;
}
.layout .ant-layout-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.layout .ant-layout-header .anticon.trigger {
  margin: 0 24px;
  font-size: 1.4em;
}
.layout .ant-layout-header .ant-btn.ant-btn-link .fa {
  margin-right: 0.5rem;
}
.layout .notification {
  position: fixed;
  right: 5px;
  bottom: 2px;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  color: #faad14;
}
.layout .notification .anticon {
  margin-right: 0.5rem;
}
